import 'lodash';

export default function (prereqs) {
  let output = '';
  let uniquePrereqs = []
  let suppressIteration = false;


  function processAttrPrereq (val, key, suppressIteration) {
    if ( key === 'Feat' || key === 'Other') {
      if ( val === 'Feat' ) val += ' feat ';
      return val;
    }
    const attrStrings = val.map( (attr, x) => {
      let string = '';
      if ( key === 'Attribute' ) {
        const attrKey = Object.keys(attr)[0];
        string += ( x === val.length - 1 ) ? ` ${ val.length > 1 ? ' or ' : '' }${ attrKey } ${ attr[attrKey] }` : ` ${ attrKey }`;
      } else if ( key === 'Feat' ) {
        string += `${ attr } feat `;
      } else if ( key === 'Other ' ) {
        string += attr;
      }
      return string;
    });
    return attrStrings.join(',');
  }

  function processPrereqTier (prereq, key, index, suppressIteration) {

    let prereqTiers = '';

    if ( key === 'any' ) {
      Object.keys( prereqs[prereq][key] ).forEach( (subKey, subKeyIndex) => {
        prereqTiers += processAttrPrereq( prereqs[prereq][key][subKey], subKey, suppressIteration );
      });
    } else {
      prereqTiers += processAttrPrereq( prereqs[prereq][key], key, suppressIteration );
    }

    return prereqTiers;
  }

  Object.keys(prereqs).forEach( key => {
    uniquePrereqs.push(prereqs[key]);
  });
  uniquePrereqs = _.uniqWith(uniquePrereqs, _.isEqual);

  if ( Object.keys(prereqs).length > uniquePrereqs.length ) {
    suppressIteration = true;
  }

  Object.keys(prereqs).forEach( (prereq, x) => {

    function processPrereq (key, i, localPrereqKeys) {

      var output = '';
      if ( i === 0 ) {
        output += `<strong>Tier ${ prereq.split('tier')[1] }</strong>: `;
      }

      if ( localPrereqKeys.length > 1 && i === 0 ) {
        output += `<ul>`;
      }


      if ( localPrereqKeys.length > 1 && i === 0 ) {
        output += `<li>`;
      }

      output += processPrereqTier(prereq, key, i, suppressIteration);

      if ( localPrereqKeys.length > 1 && i > 0 ) {
        output += `</li>`;
      }

      if ( localPrereqKeys.length > 1 && i === localPrereqKeys.length -1  ) {
        output += `</ul>`;
      }

      return output;
    }

    const prereqKeys = Object.keys(prereqs[prereq]);
    const localPrereqKeys = prereqKeys;

    if (suppressIteration && x > 0) {
      return;
    }

    output += '<ul>'

    prereqKeys.forEach( (key, z) => {
      if ( _.get(prereqs, 'tier1.Feat[0]') === 'Battlefield Defender' ) {
        console.log( 'localPrereqKeys', localPrereqKeys)
        console.log('key',key,'prereqs',prereqs)
      }
      output += `<li>${ processPrereq(key, z, localPrereqKeys) }</li>`;
    });

    output += '</ul>';
  });

  return output;
}
